module.exports = [[module.id, "/* --------------------------- CREAM NG MAIN CSS ------------------- */\n\n/* 2018 */\n\n/* CUSTOM PROPERTIES */\n\n/* --------------------------------------------------------------------\n               \t\t\tCREAMLIB\n          \t\t\t\tLe Studio, 2018\n/*-------------------------------------------------------------------- */\n\n:root {\n\t--tourquoise: 21,129,158;\n\t--tourquoise-light: 69,171,188;\n\t--green: 53,120,28;\n\t--green-light: 108,199,69;\n\t--hot-rose: 189,77,150;\n\t--light-rose: 215,105,168;\n\t--orange: 237,156,33;\n\t--dark-blu: 30,46,57;\n\t--gray-m: 204,204,204;\n\t--gray-light: 238,238,238;\n\t--gray-s-light: 239,239,239;\n\t--black: 0,0,0;\n\t--red: 204,0,0;\n\n\t--body-text: #000;\n\t--body-bg: #FFF;\n\t--text-inv: #FFF;\n\n\t--main-color: #15819E;\n\t--main-light: #45ABBC;\n\t--sec-color: #35781C;\n\t--sec-light: #6CBE45;\n\t--hot-color: #BD4D96;\n\t--hot-light: #D769A8;\n\t--hot-color-sec: #ED9C21;\n\t--n-dark: #1E2E39;\n\t--n-medium: #CCC;\n\t--n-medium-a: #6B6B6B;\n\t--n-light: #EEE;\n\t--n-lighter: #EFEFEF;\n\t--n-s-dark: #000;\n\t--alert-color: #C00;\n\n  --stack: quicksandregular, Calibri, Roboto, Helvetica, sans-serif;\n\t--stack-m: quicksandmedium, Calibri, Roboto, Helvetica, sans-serif;\n\t--stack-l: quicksandlight, Calibri, Roboto, Helvetica, sans-serif;\n\t--stack-b: quicksandbold, Calibri, Roboto, Helvetica, sans-serif;\n\t--stack-i: quicksanditalic, Calibri, Roboto, Helvetica, sans-serif;\n}\n\n/* RESET */\n\nhtml,\nbody,\ndiv,\nspan,\napplet,\nobject,\niframe,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nblockquote,\npre,\na,\nabbr,\nacronym,\naddress,\nbig,\ncite,\ncode,\ndel,\ndfn,\nem,\nimg,\nins,\nkbd,\nq,\ns,\nsamp,\nsmall,\nstrike,\nstrong,\nsub,\nsup,\ntt,\nvar,\nb,\nu,\ni,\ncenter,\ndl,\ndt,\ndd,\nol,\nul,\nli,\nfieldset,\nform,\ninput,\nlabel,\nlegend,\ntable,\ncaption,\ntbody,\ntfoot,\nthead,\ntr,\nth,\ntd,\narticle,\naside,\ncanvas,\ndetails,\nembed,\nfigure,\nfigcaption,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\noutput,\nruby,\nsection,\nsummary,\ntime,\nmark,\naudio,\nvideo,\nbutton {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  vertical-align: top;\n  box-sizing: border-box;\n}\n\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n  display: block;\n}\n\nhtml {\n  width: 100%;\n  height: 100%;\n}\n\n/* ----------- DEFAULTS ----------------- */\n\nbody {\n  font: 100%/1.2 var(--stack);\n  color: var(--body-text);\n  background-color: var(--body-bg);\n}\n\nh4 {\n  font: normal 1.15em/1.2 var(--stack-b);\n  margin-bottom: 1em;\n}\n\np,\nli,\ndt,\ndd {\n  font-size: 1em;\n}\n\nli p,\nli li,\nli dt,\nli dd {\n  font-size: 100%;\n}\n\na {\n  text-decoration: underline;\n  color: var(--n-s-dark);\n}\n\na:visited {\n  color: var(--n-dark);\n}\n\na:hover {\n  text-decoration: none;\n}\n\nol,\nul {\n  list-style: none;\n}\n\nimg {\n  max-width: 100%;\n  border: 0;\n}\n\nstrong {\n  font-family: var(--stack-b);\n  font-weight: normal;\n}\n\nem {\n  font-family: var(--stack-i);\n  font-style: normal;\n}\n\nbutton {\n  font: 100%/1 var(--stack-m);\n  padding: 0.5rem 1rem;\n  border: none;\n  border-radius: 0.3em;\n  cursor: pointer;\n}\n\nabbr {\n  cursor: help;\n}\n\nblockquote,\nq {\n  quotes: none;\n}\n\nblockquote:before,\nblockquote:after,\nq:before,\nq:after {\n  content: '';\n  content: none;\n}\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n\nhr {\n  display: none;\n}\n\na.skip_links {\n  left: -99999px;\n  position: absolute;\n}\n\ncode {\n  font: 100% 'Courrier New', Courier, monospace;\n  color: var(--sec-color);\n  background-color: var(--body-bg);\n  vertical-align: baseline;\n}\n\n.svgLibrary {\n  display: none;\n}\n\n/* ********************** SCROLLBAR/Webkit only *************** */\n\n::-webkit-scrollbar {\n  width: 16px;\n  background-color: #dbd8d8;\n}\n\n::-webkit-scrollbar:horizontal {\n  height: 16px;\n}\n\n::-webkit-scrollbar-track {\n  border: 1px #dbd8d8 solid;\n  border-radius: 10px;\n  box-shadow: 0 0 6px #dbd8d8 inset;\n  -webkit-box-shadow: 0 0 6px #dbd8d8 inset;\n}\n\n::-webkit-scrollbar-thumb {\n  background-color: #44abbc;\n  border: 1px solid #dbd8d8;\n  border-radius: 3px;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background-color: #2b9bad;\n  border: 1px solid #2b9bad;\n}\n\n::-webkit-scrollbar-thumb:active {\n  background-color: #44abbc;\n  border: 1px solid #dbd8d8;\n}\n\n/* *********** PAGE STRUCTURE ************* */\n\napp-breadcrumbs {\n  display: none;\n}\n\ndiv.demo app-breadcrumbs {\n  display: block;\n}\n\n/* *********** FORMS ************* */\n\nlabel {\n  display: block;\n  font: 90%/1 var(--stack-b);\n  padding-bottom: 0.3em;\n}\n\ninput,\nselect,\ntextarea {\n  font: 100%/1 var(--stack);\n  color: var(--n-dark);\n  background-color: var(--body-bg);\n  padding: 0.5em;\n  border: 1px solid var(--n-medium);\n  border-radius: 0.3em;\n  transition: all;\n}\n\ninput:focus,\nselect:focus,\ntextarea:focus {\n  border-color: var(--body-bg);\n  border-bottom-color: var(--sec-light);\n  border-radius: 0;\n}\n\ninput[disabled] {\n  color: var(--n-dark);\n  background-color: var(--n-medium);\n  border: none;\n}\n\n/* To structure group of buttons */\n\nul.buttons-bar li {\n  display: inline-block;\n  font-size: 1em;\n  margin: 0 0.5em 0.5em 0;\n}\n\n/* *********** ARTICLES ************* */\n\np + p {\n  margin-top: 1em;\n}\n\narticle.post {\n  background-color: var(n-lighter);\n  padding: 5vh 5%;\n}\n\narticle.post h3 {\n  display: block;\n  font: 1.3rem var(--stack-b);\n  color: var(--sec-color);\n  margin-bottom: 1em;\n}\n\narticle.post h4 {\n  font: 1rem var(--stack-b);\n  margin-bottom: 1em;\n}\n\narticle.post ul {\n  margin: 2rem 1rem;\n}\n\narticle.post li + li {\n  margin-top: 0.5em;\n}\n\narticle.post p {\n  line-height: 1.45;\n}\n\narticle.post p + p {\n  margin-top: 0.5em;\n}\n\narticle.post p + h3,\narticle.post p + h4 {\n  margin-top: 1em;\n}\n\narticle.post a {\n  font-family: var(--stack-b);\n  text-decoration: underline;\n}\n\narticle.post a:hover,\narticle.post a:focus {\n  text-decoration: none;\n}\n\narticle.post code {\n  display: inline-block;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n  background-color: var(--body-bg);\n  margin: 0 0 0.3em;\n  padding: 0.5rem 1rem;\n  border-radius: 0.3em;\n}\n\n/* Child Containers */\n\narticle.post section {\n  background-color: var(--n-lighter);\n  padding: 1rem;\n  margin-bottom: 1rem;\n}\n\narticle.post div.group {\n  max-width: 45em;\n}\n\narticle.post div.group + div.group {\n  padding-top: 1rem;\n  margin-top: 1rem;\n  border-top: 1px solid var(--n-medium);\n}\n\ndiv.code-ex {\n  margin: 1em 0;\n}\n\n/* *********** PANEL TITLE ************* */\n\n.panelTitle {\n  margin-bottom: 10vh;\n  border-top: 1px solid var(--body-bg);\n}\n\n.panelTitle h2 {\n  font: 1.2em var(--stack);\n  color: var(--text-inv);\n  background-color: var(--main-color);\n  padding: 0.5em 5%;\n  margin-bottom: 1em;\n}\n\n.panelTitle ul {\n  display: flex;\n  flex-wrap: wrap;\n  margin: 0 5%;\n  border-left: 5px solid var(--main-light);\n}\n\n.panelTitle li {\n  font: 1em var(--stack);\n  min-width: 30%;\n  flex-basis: 20%;\n}\n\n.panelTitle li a {\n  display: block;\n  text-decoration: none;\n  padding: 0.5em 1em;\n  -webkit-transform: perspective(1px) translateZ(0);\n          transform: perspective(1px) translateZ(0);\n}\n\n/* ********** TITLE COMPONENT *********** */\n\n.titleComponent {\n  background-color: var(--body-bg);\n  padding: 0 5%;\n}\n\n.titleComponent + .titleComponent {\n  margin-top: 5vh;\n}\n\n.titleComponent > div {\n  padding-top: 1rem;\n  margin-bottom: 2rem;\n  border-top: 3px dotted var(--n-dark);\n}\n\n.titleComponent > div h3 {\n  position: relative;\n  font: 1.2em var(--stack-b);\n  text-transform: uppercase;\n  padding-right: 45px;\n  margin: 0 0 1.5em 0;\n}\n\n.titleComponent > div h3 > svg {\n  /*display: inline;\n          width: 22.2px;\n          height: 32px;\n\t\t\t\t\tfill: var(--main-light);\n          vertical-align: bottom;*/\n  display: none;\n}\n\n/* GitHub Button */\n\n.titleComponent .gitBtn {\n  display: none;\n  position: absolute;\n  overflow: hidden;\n  width: 35px;\n  height: 35px;\n  top: 0;\n  right: 0;\n  text-transform: none;\n  color: var(--main-color);\n  background-color: var(--n-lighter);\n  border-radius: 0.3em;\n  border: none;\n  transition-property: color;\n  transition-duration: 0.3s;\n  -webkit-transform: perspective(1px) translateZ(0);\n          transform: perspective(1px) translateZ(0);\n}\n\n/* Git Hub IMG */\n\n.titleComponent .gitBtn svg {\n  display: none;\n  position: relative;\n  top: 6px;\n  left: 6px;\n  width: 35px;\n  height: 30px;\n  fill: var(--n-dark);\n  transition: fill 0.3s ease-out;\n}\n\n.titleComponent .gitBtn span {\n  position: absolute;\n  left: 150px;\n}\n\n/* GitHub Button Hover */\n\n.titleComponent .gitBtn:hover {\n  color: var(--body-bg);\n}\n\n.titleComponent .gitBtn:hover svg {\n  fill: var(--body-bg);\n}\n\n.titleComponent .gitBtn::before {\n  content: '';\n  position: absolute;\n  z-index: -1;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  border-radius: 3px;\n  background: var(--main-color);\n  -webkit-transform: scale(0);\n          transform: scale(0);\n  transition-property: -webkit-transform;\n  transition-property: transform;\n  transition-property: transform, -webkit-transform;\n  transition-duration: 0.3s;\n  transition-timing-function: ease-out;\n}\n\n.titleComponent .gitBtn:hover::before,\n.titleComponent .gitBtn:focus::before,\n.titleComponent .gitBtn:active::before {\n  -webkit-transform: scale(1);\n  transform: scale(1);\n}\n\n.titleComponent > div > p,\n.titleComponent div.explain > p {\n  max-width: 40em;\n  line-height: 1.45;\n  padding-right: 50px;\n}\n\n.titleComponent > div > p > strong {\n  color: var(--n-dark);\n}\n\n.titleComponent > div > p > a {\n  text-decoration: underline;\n}\n\n/* DEMO BLOCK */\n\ndiv.demo {\n  background: var(--n-lighter) url('demo-bkgd.png');\n  padding: 1rem;\n  margin: 0 0 2rem;\n}\n\ndiv.explain {\n  margin-bottom: 2rem;\n}\n\ndiv.explain p {\n  margin-bottom: 1rem;\n}\n\ndiv.explain dl div {\n  display: table-row;\n}\n\ndiv.explain dl {\n  margin-bottom: 1rem;\n}\n\ndiv.explain dt,\ndiv.explain dd {\n  display: table-cell;\n  font-size: 1em;\n  padding: 0.5em 0;\n  border-bottom: 1px solid var(--n-medium);\n}\n\ndiv.explain dt {\n  padding-right: 1em;\n}\n\ndiv.explain p + p {\n  margin-top: 0.8em;\n}\n\ndiv.explain dt {\n  font-family: var(--stack-b);\n  color: var(--n-dark);\n  padding-right: 1em;\n}\n\ndiv.explain a,\ndiv.explain a:visited {\n  text-decoration: underline;\n}\n\ndiv.demo .explain {\n  font-size: 1em;\n}\n\n/* ------------- TIPS/BEST PRACTICES --------------- */\n\ndiv.tip {\n  display: flex;\n  background-color: rgba(var(--green), 0.1);\n  padding: 1rem;\n}\n\ndiv.tip svg {\n  min-width: 46px;\n  margin-right: 1rem;\n}\n\ndiv.tip p {\n  line-height: 1.5;\n}\n\n/* **************** CODE SNIPPPEETTSS *************** */\n\n.pre-code {\n  font: 1.1em var(--stack);\n  margin: 0 0 1rem;\n}\n\n.pre-code strong {\n  color: var(--sec-color);\n}\n\nol.linenums code {\n  font: 1em var(--stack);\n  background-color: rgba(255, 255, 255, 0);\n}\n\n/* ************ CODE SNIPPETS IN TABS COMPONENTS ********** */\n\nc3m-tabs div.code {\n  overflow: auto;\n  width: 100%;\n  max-height: 300px;\n  color: var(--text-inv);\n  background-color: var(--n-dark);\n  margin: 0 0 1rem;\n}\n\nc3m-tabs ol.display-code {\n  list-style: decimal;\n  margin-left: 2.2rem;\n}\n\nc3m-tabs ol.display-code li {\n  padding: 0.4rem 0.4rem 0.4rem 1rem;\n  margin-left: -0.2rem;\n}\n\nc3m-tabs ol.display-code pre span {\n  color: #f7901e;\n}\n\nc3m-tabs ol.display-code em {\n  color: #2ad020;\n}\n\n/* TO GENERATE SNIPPETS CODE  */\n\nc3m-tabs .str {\n  color: #ef86d7;\n}\n\nc3m-tabs .kwd {\n  color: #6cd839;\n}\n\nc3m-tabs .com {\n  color: #3fcfe6;\n}\n\nc3m-tabs .typ {\n  color: #3fcfe6;\n}\n\nc3m-tabs .lit {\n  color: #f59e17;\n}\n\nc3m-tabs .pun {\n  color: #f1f2f3;\n}\n\nc3m-tabs .pln {\n  color: #f1f2f3;\n}\n\nc3m-tabs .tag {\n  color: #6cd839;\n}\n\nc3m-tabs .atn {\n  color: #e0e2e4;\n}\n\nc3m-tabs .atv {\n  color: #f59e17;\n}\n\nc3m-tabs .dec {\n  color: #ef86d7;\n}\n\nc3m-tabs pre.prettyprint {\n  overflow-x: auto;\n}\n\nc3m-tabs .content .code {\n  overflow-x: hidden;\n}\n\nc3m-tabs ol.linenums {\n  margin-top: 0;\n  margin-bottom: 0;\n  display: inline-block;\n  min-width: 100%;\n}\n\nc3m-tabs .prettyprint {\n  background: var(--n-dark);\n}\n\nc3m-tabs li.L0,\nc3m-tabs li.L1,\nc3m-tabs li.L2,\nc3m-tabs li.L3,\nc3m-tabs li.L4,\nc3m-tabs li.L5,\nc3m-tabs li.L6,\nc3m-tabs li.L7,\nc3m-tabs li.L8,\nc3m-tabs li.L9 {\n  padding: 0.2rem 0;\n}\n\nc3m-tabs li.L1,\nc3m-tabs li.L3,\nc3m-tabs li.L5,\nc3m-tabs li.L7,\nc3m-tabs li.L9 {\n  background: var(--n-dark);\n  padding: 0.2rem 0;\n}\n\n/* GITHUB FORK BUTTON */\n\n.github-corner:hover .octo-arm {\n  -webkit-animation: none;\n          animation: none;\n}\n\n.github-corner .octo-arm {\n  -webkit-animation: octocat-wave 560ms ease-in-out;\n          animation: octocat-wave 560ms ease-in-out;\n}\n\n@-webkit-keyframes octocat-wave {\n  0%,\n  100% {\n    -webkit-transform: rotate(0);\n            transform: rotate(0);\n  }\n\n  20%,\n  60% {\n    -webkit-transform: rotate(-25deg);\n            transform: rotate(-25deg);\n  }\n\n  40%,\n  80% {\n    -webkit-transform: rotate(10deg);\n            transform: rotate(10deg);\n  }\n}\n\n@keyframes octocat-wave {\n  0%,\n  100% {\n    -webkit-transform: rotate(0);\n            transform: rotate(0);\n  }\n\n  20%,\n  60% {\n    -webkit-transform: rotate(-25deg);\n            transform: rotate(-25deg);\n  }\n\n  40%,\n  80% {\n    -webkit-transform: rotate(10deg);\n            transform: rotate(10deg);\n  }\n}\n\n/* *********** QUERIES ************* */\n\n@media (max-width: 660px) {\n  .panelTitle li {\n    min-width: 50%;\n  }\n}\n\n@media (max-width: 460px) {\n  .panelTitle li {\n    min-width: 100%;\n  }\n}\n\n@media screen and (min-width: 750px) {\n  h4 {\n    font-size: 1.4em;\n  }\n\n  p,\n  li,\n  dt,\n  dd {\n    font-size: 1.3em;\n  }\n\n  ol.linenums li {\n    font-size: 1em;\n  }\n\n  div.explain dt,\n  div.explain dd {\n    font-size: 1em;\n  }\n\n  .titleComponent > div h3 {\n    font-size: 1.6em;\n  }\n\n  c3m-tabs div.code {\n    max-height: 500px;\n  }\n\n  c3m-tabs ol.display-code {\n    margin-left: 0;\n  }\n\n  .pre-code {\n    font: 1.1em var(--stack);\n    margin: 1rem auto;\n  }\n\n  div.tip p {\n    font-size: 1em;\n  }\n}\n\n@media screen and (min-width: 850px) {\n  div.explain {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: space-between;\n    align-items: start;\n  }\n\n  div.explain p {\n    flex-basis: 100%;\n  }\n\n  div.tip {\n    flex-basis: 30%;\n  }\n}\n\n@media screen and (min-width: 1000px) {\n  .titleComponent .gitBtn {\n    display: none;\n    width: 5.8em;\n    font-size: 75%;\n    text-decoration: none;\n    color: var(--n-dark);\n  }\n\n  .titleComponent .gitBtn svg {\n    left: 10px;\n  }\n\n  .titleComponent .gitBtn span {\n    position: static;\n    text-indent: 0;\n    line-height: 2em;\n  }\n\n  div.panelTitle {\n    padding: 2rem 5%;\n    margin-bottom: 5vh;\n  }\n\n  div.panelTitle h2 {\n    font-size: 2em;\n    padding: 0.5em 2rem;\n  }\n\n  .panelTitle ul {\n    padding: 0.5em 1rem;\n    margin: 0;\n  }\n\n  app-root {\n    display: flex;\n    flex-wrap: wrap;\n  }\n\n  app-header {\n    flex-basis: 25%;\n    z-index: 2;\n  }\n\n  app-main {\n    width: 75%;\n  }\n\n  app-footer {\n    flex-basis: 100%;\n    z-index: 1;\n  }\n\n  article.post section {\n    padding: 2rem;\n  }\n\n  article.post h3 {\n    font-size: 1.5em;\n    margin-bottom: 2em;\n  }\n\n  article.post h4 {\n    font-size: 1.4em;\n  }\n\n  article.post div.group + div.group {\n    padding-top: 2rem;\n    margin-top: 2rem;\n  }\n}\n\n@media screen and (min-width: 1400px) {\n  app-header {\n    flex-basis: 20%;\n  }\n\n  app-main {\n    width: 80%;\n  }\n}\n\n/* PROPERTIES TABLE */\n\n.properties table {\n  width: 100%;\n  margin-bottom: 2em;\n}\n\n.properties table th,\n.properties table td {\n  border: 1px solid #e5ebf0;\n  padding: 0.5em;\n}\n\n.properties table th {\n  background-color: var(--main-light);\n}\n\n.properties table tr:nth-child(odd) {\n  background-color: #fff;\n}\n\n.properties table tr:nth-child(even) {\n  background-color: var(--n-light);\n}\n\n.properties table td ul {\n  display: block;\n  padding-left: 1em;\n}\n\n.properties table td ul li {\n  list-style-type: disc;\n  font-size: 1em;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]